import {NearbyUser} from "./nearby-user";
/**
 * Created by vidhyasagar on 10.11.17.
 */

export class Item{

    _xAxis:number = 0;
    _yAxis:number = 0;
    _nearbyUser:NearbyUser;


constructor(){

}


  get xAxis(): number {
    return this._xAxis;
  }

  set xAxis(value: number) {
    this._xAxis = value;
  }

  get yAxis(): number {
    return this._yAxis;
  }

  set yAxis(value: number) {
    this._yAxis = value;
  }

  get nearbyUser(): NearbyUser {
    return this._nearbyUser;
  }

  set nearbyUser(value: NearbyUser) {
    this._nearbyUser = value;
  }
}



