import {Injectable} from "@angular/core";
import { StompService } from 'ng2-stomp-service';
import 'rxjs/add/operator/map';
import {Observable} from "rxjs/Observable";
import {Subject} from "rxjs/Subject";


/**
 * Created by vidhyasagar on 06.04.18.
 */

@Injectable()
export class WebSocketService{

  private serverUrl = 'http://localhost:8080/socket'

  public dataSubscription = new Subject<any>();
   subscription:any;

  constructor(private stomp: StompService){

    //configuration
    stomp.configure({
      host: this.serverUrl,
      debug:true,
      queue:{'init':false}
    });


    //start connection
    stomp.startConnect().then(() => {
      stomp.done('init');
      console.log('connected');

      //subscribe
      stomp.subscribe('/chat', this.response);

      // register the user in WebSocketSession.
      this.registerUser();


      //send data
      // stomp.send('destination',{"data":"data"});

      // to close the connection properly

      //TODO unsubscribe the user after logout or inactive mode

      // //unsubscribe
      // this.subscription.unsubscribe();
      //
      // //disconnect
      // stomp.disconnect().then(() => {
      //   console.log( 'Connection closed' )
      // })

    });
  }


  //response
  public response = (data) => {
    // console.log(data);

    this.dataSubscription.next(data);

  }

  /**
   *
   * @returns {any}
   */
  getDataSubscription(): Observable<any> {
    return this.stomp.subscribe('/chat', this.response);
  }
  /**
   * send data/messages in web socket
   *
   * @param message
   */
  sendData(message:any){

    this.stomp.send('/app/send/message', message);

    // this.stompClient.send("/app/send/message" , {}, message);

  }

  /**
   * register the user in WebSocketSession.
   *
   * @param message
   */
  registerUser(){

    this.stomp.send('/app/register/user',"hallo");

    // this.stompClient.send("/app/send/message" , {}, message);

  }


}



