import {Injectable} from '@angular/core';
import {HttpClient, HttpRequest, HttpEvent} from '@angular/common/http';
import {Observable} from 'rxjs/Observable';
import {ResponseContentType, ResponseType} from '@angular/http';
import {ImageGalleryServiceI} from './image-gallery.service.interface';

/**
 * Created by vidhyasagar on 29.03.18.
 */
@Injectable()
export class ImageGalleryService {
  visibleImages = [];

  constructor(private _http:HttpClient){

  }

  /**
   *
   * @param {File} file
   * @returns {Observable<HttpEvent<{}>>}
   */
  uploadImage(file: File): Observable<HttpEvent<{}>> {
    let formdata: FormData = new FormData();

    formdata.append('file', file);

    const req = new HttpRequest('POST', 'http://localhost:8080/image_gallery/upload', formdata, {
      reportProgress: true,
      responseType: 'text'
    });

    return this._http.request(req);
  }

  /**
   *
   * @returns {Observable<Object>}
   */
  getImages():Observable<Object> {

    return this._http.get("/assets/version-1.1/images/image-gallery");
  }

  /**
   *
   * @param {string} imageUrl
   * @returns {Observable<File>}
   */
  getImage(imageName: string): Observable<Blob> {
    return this._http
      .get("http://localhost:8080/image_gallery/get/image/"+imageName, { responseType: 'blob' });
  }


  /**
   *
   * @returns {{id: number; category: string; caption: string; url: string}[]}
   */
  // getImages() {
  //   return this.visibleImages = IMAGES.slice(0);
  // }

  // getSingleImage(id: number) {
  //   return IMAGES.slice(0).find(images => images.id == id);
  // }
}

