import {Message} from '../../Services/ChatServices/MessageService/message';

export class ChatBox{

    _id: number;
    _emailId : string;
    _conversationId : string;
    _name: string;
    _imageSrc : string;
    _messages : Array<Message> = [];
    _tempMessage: string = "";
    private _mainPanelVisibility:boolean = true;


  constructor() {
  }

  get id(): number {
    return this._id;
  }

  set id(value: number) {
    this._id = value;
  }

  get name(): string {
    return this._name;
  }

  set name(value: string) {
    this._name = value;
  }

  get imageSrc(): string {
    return this._imageSrc;
  }

  set imageSrc(value: string) {
    this._imageSrc = value;
  }

  get messages(): Array<Message> {
    return this._messages;
  }

  set messages(value: Array<Message>) {
    this._messages = value;
  }

  get tempMessage(): string {
    return this._tempMessage;
  }

  set tempMessage(value: string) {
    this._tempMessage = value;
  }

 get emailId(): string {
    return this._emailId;
  }

  set emailId(value: string) {
    this._emailId = value;
  }

  get conversationId(): string {
    return this._conversationId;
  }

  set conversationId(value: string) {
    this._conversationId = value;
  }

  get mainPanelVisibility(): boolean {
    return this._mainPanelVisibility;
  }

  set mainPanelVisibility(value: boolean) {
    this._mainPanelVisibility = value;
  }
}



