import {Component, OnInit} from '@angular/core';
import {FreelancingProject} from './freelancing-project';
import {FreelancingProjectService} from '../Services/FreelancingProjectService/freelancing-project-service';

@Component({
  selector: 'app-freelancing-projects',
  templateUrl: './freelancing-projects.component.html',
  styleUrls: ['./freelancing-projects.component.css']
})
export class FreelancingProjectsComponent implements OnInit {

  freelancingProjects: FreelancingProject[] = [];

  constructor(private _freelancingProjectService:FreelancingProjectService) {
  }

  ngOnInit() {
    this.createDummyProject();
    this.getFreelancingProjects();
  }

  /**
   * create dummy project to test
   */

  createDummyProject() {

    let freelancingProjectDummy: FreelancingProject
      = {
      'id': '5af7533459eb228ac50b6d8d',
      'title': 'Java Developer',
      'category': 'IT',
      'description': 'Needed Backend developer with 3 years of experience!',
      'image_url': '',
      'posted_by': 'vidhyasgr80@gmail.com',
      'start_time': '05/05/2018 10:10:10',
      'end_time': '05/05/2018 12:10:10',
      'location': 'Frankfurt',
      'is_remote_possible': true,
      'money': '5000 Euro',
      'created_time': '05/05/2018 10:10:10',
      'tags': ['java', 'node.js', 'spring boot', 'angular 5', 'html and css'],
      'is_interested': false
    };

    this.freelancingProjects.push(freelancingProjectDummy);
  }

  /**
   *
   * @param {FreelancingProject} freelancingProject
   */
  expressInterest(freelancingProject: FreelancingProject) {

    freelancingProject.is_interested = !freelancingProject.is_interested;

  }

  /**
   *
   */
  getFreelancingProjects(){
    this._freelancingProjectService
      .getFreelancingProjects()
      .subscribe((data) => {
        this.freelancingProjects = data;
      });
  }

}



