import {Component, Input, OnInit, ViewEncapsulation} from '@angular/core';
import {ImageGalleryService} from "../Services/ImageServices/image-gallery-service";
import {HttpEventType, HttpResponse} from '@angular/common/http';
import {Image} from './image';

@Component({
  selector: 'app-image-gallery',
  templateUrl: './image-gallery.component.html',
  styleUrls: ['./image-gallery.component.css'],
  encapsulation: ViewEncapsulation.None
})
export class ImageGalleryComponent implements OnInit {

  private images: Image[] = [];
  currentImageUpload: File
  progress: { percentage: number } = { percentage: 0 }

  showFile = false

  //after click show full screen mode
  @Input()
  showFullScreenMode:boolean = false;

  visibleImages : any[] = [];

  constructor(private imageGalleryService : ImageGalleryService) {

   }

  ngOnInit() {
    //just for mock
    this.showAllImages();
  }

  /**
   *
   * @param event
   */
  selectImage(event) {
    const file = event.target.files.item(0)

    if (file.type.match('image.*')) {
       this.images.push(event.target.files);
    } else {
      alert('invalid format!');
    }
  }

  /**
   *
   * @param {Blob} image
   */

  createImageFromBlob(image: Blob) {
    let reader = new FileReader();
    reader.addEventListener("load", () => {
      let image:Image = new Image();
      image.imageData = reader.result;
      //todo need to fetch data from REST api.
      image.title = "Mountain";
      image.description = "its a beautiful picture";

      this.images.push(reader.result);
    }, false);

    if (image) {
      reader.readAsDataURL(image);
    }
  }


  /**
   *
   * @param {boolean} enable
   */
  showImage(enable: boolean) {

        this.imageGalleryService.
        getImage("Bildschirmfoto 2018-05-06 um 22.04.44.png").subscribe(
          imageData => {
            this.createImageFromBlob(imageData);
          }
        );

  }

  /**
   *
   * mocking all images. just for test purpose
   *
   */
  showAllImages(){

    for (let i = 1 ; i < 6; i++) {
      // this.images.push("../assets/version-1.1/images/image-gallery/" + i + ".jpg");

      let image:Image = new Image();
      image.description= "Its a wonderful moment!";
      image.title = "picture " + i;
      image.imageData = "../assets/version-1.1/images/image-gallery/" + i + ".jpg";
      image.tags[0] = "natural";

      this.images.push(image);

    }

  }


  /**
   *
   * after click, just upload the image in db.
   *
   */
  upload() {
    this.progress.percentage = 0;

    this.imageGalleryService.uploadImage(this.currentImageUpload).subscribe(event => {
      if (event.type === HttpEventType.UploadProgress) {
        this.progress.percentage = Math.round(100 * event.loaded / event.total);
      } else if (event instanceof HttpResponse) {
        console.log('File is completely uploaded!');
      }
    })

  }

}



