import {Component, OnInit} from '@angular/core';
import {ChatBox} from './message.model';
import {MessageService} from '../../Services/ChatServices/MessageService/message.service';
import {Message} from '../../Services/ChatServices/MessageService/message';
import {WebSocketService} from '../../Services/WebSocketService/web-socket-service';

@Component({
  selector: 'app-chat-box',
  templateUrl: './chat-box.component.html',
  styleUrls: ['./chat-box.component.css']
})


export class ChatBoxComponent implements OnInit {

  textBoxValue:string = "";

  constructor(public _messageService: MessageService,
              private _webSocketService:WebSocketService) {
  }

  ngOnInit() {


    // subscribe to chat message via WebSocket
    this._webSocketService.dataSubscription.subscribe(data =>{

      // after receiving the message store it in local.
      // this._messageService.storeNewMessage(data);

    })

  }

  /**
   *
   * @param text
   * @param chatBoxModel
   */

  sendNewMessage(text: string, chatBoxModel: ChatBox) {

  if(text != "" && text != null) {
      // create new message model using chatBoxModel.
      let newMessage = this.buildMessageModel(text, chatBoxModel);
      //Empty the text box.
        this.textBoxValue = "";
      //add new messages in local as well as in remote.
      this._messageService.storeNewMessage(newMessage);
    }
    }

  /**
   *
   * @param {string} text
   * @returns {Message}
   */
  private buildMessageModel(text: string, chatBoxModel: ChatBox) {
    let newMessage: Message = new Message();
    newMessage.body = text;
    newMessage.conversation_id = chatBoxModel._conversationId;
    newMessage.to = chatBoxModel._emailId;
    newMessage.sentTime = new Date().toDateString();
    return newMessage;
  }

  /**
   *
   * @param chatBox
   */

  changeInHiddenPanelVisibility(chatBox: ChatBox) {
    chatBox.mainPanelVisibility = true;
  }

  /**
   *
   * @param chatBox
   */

  changeInVisibilityByMinimizing(chatBox: ChatBox) {

    // hiding the main panel after clicking the minimize button nd showing the hiddenpanel.
    chatBox.mainPanelVisibility = false;
  }

  /**
   *
   * @param chatBox
   */
  changeInVisibilityByClosing(chatBox: ChatBox) {

    //close main as well as hidden panels
    chatBox.mainPanelVisibility = false;
  }



}



