import {Component, OnInit} from '@angular/core';
import {ChatBox} from './chat-box/message.model';
import {MessageService} from '../Services/ChatServices/MessageService/message.service';
import {
  trigger, state, style, animate, transition
} from '@angular/animations';
import {Message} from '../Services/ChatServices/MessageService/message';
import {FriendList} from './friend-list';

@Component({
  selector: 'app-chat-room',
  templateUrl: './chat-room.component.html',
  styleUrls: ['./chat-room.component.css'],
  animations: [trigger('slideInOut', [state('in', style({
    transform: 'translate3d(0, 0, 0)'
  })), state('out', style({
    transform: 'translate3d(-82%, 0, 0)'
  })), transition('in => out', animate('400ms ease-in-out')), transition('out => in', animate('400ms ease-in-out'))])]
})
export class ChatRoomComponent implements OnInit {
  name: string;
  menuState: string = 'in';

  constructor(public _messageService: MessageService) {
    // this.name = 'Rahul';
  }

  ngOnInit() {
    this._messageService.getFriendLists().subscribe(data => {
      console.log('friends list data ' + data);
      this._messageService.friendLists = data;
    });
  }

  /**
   *
   * toggling menu to show and hiding the friend list.
   *
   */
  toggleMenu() {
    this.menuState = this.menuState === 'out' ? 'in' : 'out';
  }

  /**
   *
   * update chat box data after clicking on chat list name
   *
   * @param chatListName
   */
  clickOnChatListName(contactList: FriendList) {


    // initialize ChatBox.
    var chatBox = this.initializeChatBox(contactList);
    // check chatBoxView and open/add chatBox if it s less than 12 opened.
    if (this.canChatBoxOpen(contactList) && this._messageService.chatBoxes.length < 12) {

      // get conversation ID.
      this._messageService.getNewConversationListIdFromDB(chatBox.emailId).subscribe(data => {
        let conversationId = data.conversation_id;
        // fetch all messages from DB and inject it to chatBox.
        this._messageService.fetchAllMessagesFromDB(conversationId).subscribe(data => {
          chatBox._messages = data;
          // add the chatBox in chatBoxes list.
          this._messageService.addChatBox(chatBox);
        });
      })
    }
  }

  /**
   *
   * check chat box already open or not. If yes, then say no.
   *
   * @param {FriendList} contactList
   * @returns {boolean}
   */
  private canChatBoxOpen(contactList: FriendList) {
    var isChatBoxAlreadyOpen = true;
    this._messageService.chatBoxes.forEach(chatBoxModelData => {
      isChatBoxAlreadyOpen = this.checkAndSetView(chatBoxModelData, contactList, isChatBoxAlreadyOpen);
    });
    return isChatBoxAlreadyOpen;
  }

  /**
   *
   * @param chatBoxModelData
   * @param {FriendList} contactList
   * @param {boolean} showChatBox
   * @returns {boolean}
   */
  private checkAndSetView(chatBoxModelData,
                          contactList: FriendList,
                          showChatBox: boolean) {
    if (chatBoxModelData.name == contactList.first_name) {
      showChatBox = false;
      this.setPanelViewInChatBox(chatBoxModelData);
    }
    return showChatBox;
  }

  /**
   *
   * @param chatBoxModelData
   */
  private setPanelViewInChatBox(chatBoxModelData) {
//TODO Make it visible.
    chatBoxModelData.mainPanelVisibility = true;
  }

  /**
   * initalize the chat box with all details
   *
   * @param {FriendList} chatList
   * @returns {ChatBox}
   */
  private initializeChatBox(chatList: FriendList) {
    let chatBox: ChatBox = new ChatBox();
    let message = this.initializeMessageModel();
    chatBox.emailId = chatList.email_id;
    chatBox.name = chatList.first_name;
    chatBox.conversationId = chatList.conversation_id;
    chatBox.messages.push(message);
    chatBox.mainPanelVisibility = true;
    return chatBox;
  }

  /**
   * initialize the message in chat box.
   *
   * @returns {Message}
   */
  private initializeMessageModel() {
    let message: Message = new Message();
    message.sentTime = Date.now().toString();
    message.body = 'Hi, how are you ?';
    return message;
  }
}



