export class Message {
  private _id: string;
  private _conversation_id: string;
  private _to: string;
  private _from: string;
  private _body: string;
  private _sentTime: string;

  get id(): string {
    return this._id;
  }

  set id(value: string) {
    this._id = value;
  }

  get conversation_id(): string {
    return this._conversation_id;
  }

  set conversation_id(value: string) {
    this._conversation_id = value;
  }

  get to(): string {
    return this._to;
  }

  set to(value: string) {
    this._to = value;
  }

  get from(): string {
    return this._from;
  }

  set from(value: string) {
    this._from = value;
  }

  get body(): string {
    return this._body;
  }

  set body(value: string) {
    this._body = value;
  }

  get sentTime(): string {
    return this._sentTime;
  }

  set sentTime(value: string) {
    this._sentTime = value;
  }
}




