import {Injectable} from '@angular/core';
import {Subject} from 'rxjs/Subject';
import {Observable} from 'rxjs/Observable';
import {ChatBox} from '../../../chat-room/chat-box/message.model';
import {FriendList} from '../../../chat-room/friend-list';
import {Message} from './message';
import {HttpClient} from '@angular/common/http';
import {MessageServiceI} from './message.service.interface';
import {WebSocketService} from '../../WebSocketService/web-socket-service';
// this service contains incoming and outgoing messages. At the same time it takes care of
// chatting conversation.
// It doesnt store the messages. Just passing the messages between the components.
export interface WebSocketMessage {

  body:string;
  to:string;
  sentTime:string;

}


@Injectable()
export class MessageService implements MessageServiceI {
  friendLists: FriendList[];
  chatBoxes: Array<ChatBox> = [];
  messages = new Subject<any>();

  constructor(private _http: HttpClient,
              private _webSocketService:WebSocketService) {
  }

  /**
   *
   * @param message
   */

  addChatBox(message: any) {
    this.messages.next(message);
    // Storing values in central array, so that we could access all around the app.
    this.chatBoxes.push(message);
  }

  /**
   *
   * @param message
   */

  storeNewMessageInLocalDB(newMessage: Message) {
    this.chatBoxes.forEach(chatBox => {
      console.log('test to email address ' + chatBox.emailId + ' ' + newMessage.to);
      if (chatBox.emailId == newMessage.to) {
        chatBox.messages.push(newMessage);
      }
    });
  }

  /**
   *
   * store messages in LocalDB as well as in RemoteDB
   *
   * @param chatBox
   * @param newMessage
   */

  storeNewMessage(newMessage: Message) {

    //TODO Refactor it.

   let webSocketMessage: WebSocketMessage;
   webSocketMessage = newMessage;

    //todo Send the message in socket.io
    this._webSocketService.sendData(webSocketMessage);

    console.log("message to email id " + newMessage.to);

    //TODO Need to do right exception handler here.
    // store the message in localDB
    this.storeNewMessageInLocalDB(newMessage);
    //check whether the new Message has conversationId.
    if (newMessage.conversation_id == null) {
      //get conversation_id from mongoDB.
      this.getNewConversationListIdFromDB(newMessage.to).subscribe(data => {

        // update the message DTO with conversationID.
      newMessage.conversation_id = data.conversation_id;
      //update the localDB as well.
        this.addNewConversationId(newMessage);
        //store it in localDB and remoteDB.

        this.storeInRemoteDB(newMessage);
      })
    } else {
      //store it in localDB and remoteDB.
      this.storeInRemoteDB(newMessage);
    }
  }

  /**
   *
   * @param {Message} newMessage
   */
  private storeInRemoteDB(newMessage: Message) {
//Store in LocalDB
    //Read and Convert it to JSON
    let messageToStoreInDB = this.convertMessageToJson(newMessage);
    //Store in remoteDB (MongoDB)
    this.storeMessageInDB(messageToStoreInDB);
  }

  /**
   *
   * @param {Message} newMessage
   * @param newConversationId
   */
  private addNewConversationId(newMessage: Message) {
    this.friendLists.forEach(friendList => {
      if (friendList.email_id == newMessage.to) {
        friendList.conversation_id = newMessage.conversation_id;
      }
    })
  }

  /**
   *
   * @param {Message} newMessage
   * @returns {any}
   */
  private convertMessageToJson(newMessage: Message) {
    let messageToStoreInDB =
      this.generateMessageJSON(newMessage);
    return messageToStoreInDB;
  }

  /**
   *
   * @param messageToStore
   * @returns {string}
   */
  private generateMessageJSON(messageToStore: Message) {
    let messageToStoreInDB: string = '{"to" : "' + messageToStore.to + '","conversation_id" ' +
      ': "' + messageToStore.conversation_id + '","from" : "","body" : "' + messageToStore.body + '","sent_time" : "22/5/2018"}';
    return JSON.parse(messageToStoreInDB);
  }

  /**
   * getting contact lists.
   *
   * @returns {Observable<Object>}
   */
  getFriendLists(): Observable<FriendList[]> {
    return this.getFriendListsFromDB();
  }

  /**
   *
   */

  clearMessages() {
    this.messages.next();
  }

  getMessage(): Observable<any> {
    return this.messages.asObservable();
  }

  /**
   * store messages in mongoDB
   *
   * needs to pass the messages to be stored in mongoDB in param
   * @param {string} messageToStore
   */
  storeMessageInDB(messageToStore: any) {
    this._http.post('http://localhost:8080/userMessages/store',
      messageToStore).subscribe();
  }

  /**
   *
   * it fetches all messages from mongoDB using toAddressEmailID.
   *
   * pass toAddressEmailID as parameter
   * @param {string} conversationId
   * @returns {Observable<any[]>}
   */
  fetchAllMessagesFromDB(conversationId: string): Observable<any[]> {
    return this._http
      .get<Message[]>
      ('http://localhost:8080/userMessages/get?conversationId=' + conversationId);
  }

  /**
   * gets all contact lists first to show in frontEnd,
   * so the user can choose the remote user to chat.
   *
   * @returns {Observable<any[]>}
   */
  getFriendListsFromDB(): Observable<any[]> {
    return this._http
      .get<FriendList[]>
      ('http://localhost:8080/user_contact/get/contact_lists');
    // return this._http
    //   .get<FriendList[]>
    //   ('./assets/data/contact_lists_mockdata.json');
  }

  /**
   * it adds new conversation list,
   * so we will have conversationListId to store all conversation messages.
   *
   * needs to pass remoteUserEmailId in parameter to recognize.
   * @param {string} remoteUserEmailId
   */
  getNewConversationListIdFromDB(remoteUserEmailId: string): Observable<any> {
    return this._http
      .get<any>('http://localhost:8080/userConversationList/get?emailId=' + remoteUserEmailId);
  }
}



